 -->
<template>
  <div class="goods" id="goods" name="goods">

      <div v-if="IsShop">
              <div class="about" id="about" name="about">
        <div class="about-header">
          <div class="about-title">
            <i class="el-icon-goods" style="color: #ff6700;"></i>
            {{shopName}}
     
          </div>
    
        </div>
 
      </div>
                <!-- 分类标签 -->
                <div class="nav">
                  <div class="product-nav">
                    <el-tabs   @tab-click="handleClick" value="first">
                      <el-tab-pane label="店铺商品" name="first"> </el-tab-pane>
                      <el-tab-pane label="添加商品" name="first2"> </el-tab-pane>
                      <el-tab-pane label="店铺订单" name="second"> </el-tab-pane>
                      <el-tab-pane label="店铺详情" name="third"> </el-tab-pane>
                    </el-tabs>
                  </div>
                </div>
                <!-- 分类标签END -->

                <!-- 主要内容区 -->
                <div class="main" v-show="isshow">
                  <div class="list">
                    <MyList :list="product" v-if="product.length>0" ></MyList>
                    <div v-else class="none-product">抱歉没有找到相关的商品，请看看其他的商品</div>
                  </div>
                  <!-- 分页 -->
                  <div class="pagination">
                    <el-pagination
                      background
                      layout="prev, pager, next"
                      :page-size="pageSize"
                      :total="total"
                      :on-change="handleChange"
                      :file-list="fileList"
                      @current-change="currentChange"
                    ></el-pagination>
                  </div>
                  <!-- 分页END -->
                </div>
                <!-- 添加商品 -->
                <div class="main" v-show="addshow">
                  <br><br> 
                  <span>商品图片</span>
                <el-upload
                  class="upload-demo"
                  :before-upload="beforeupload"
                  show-file-list="false"
                  drag
                  action="http://localhost:8888/file/images"
                  style="margin-left:80px;">
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                  <!--<div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
                </el-upload>
                <el-form :model="AddItem" :rules="rules" status-icon ref="ruleForm" class="demo-ruleForm">
                        <el-form-item prop="name">
                          <el-input prefix-icon="el-icon-user-solid" placeholder="请输入商品名称" v-model="AddItem.name"></el-input>
                        </el-form-item>
                       <el-form-item prop="introduce">
                          <el-input
                            prefix-icon="el-icon-view"
                            placeholder="请输入商品简介"
                            v-model="AddItem.introduce"
                          ></el-input>
                        </el-form-item>
                       <el-form-item prop="introducemore">
                          <el-input
                            prefix-icon="el-icon-view"
                            placeholder="请输入商品详情"
                            v-model="AddItem.introducemore"
                          ></el-input>

                        </el-form-item>
                        <el-form-item prop="price">
                          <el-input prefix-icon="el-icon-user-solid" placeholder="请输入商品价格" v-model="AddItem.price"></el-input>
                        </el-form-item>
                            <el-select v-model="AddItem.category" placeholder="请选择活动区域">
                              <el-option label="丹药" value="15"></el-option>
                              <el-option label="炼丹炉" value="16"></el-option>
                              <el-option label="功法" value="17"></el-option>
                              <el-option label="异火" value="18"></el-option>
                              <el-option label="武器" value="19"></el-option>
                              <el-option label="灵宠" value="20"></el-option>
                              <el-option label="其他" value="21"></el-option>
                            </el-select>
                          <br><br><br>
                         <el-form-item prop="dl">
                          <el-button size="medium" type="primary" @click="establish('ruleForm')" style="width:100%;">创建</el-button>
                        </el-form-item>  
                 </el-form>



                </div>
 

                <!-- 添加商品 END -->
                <div class="main" v-show="introduceshow">
                  {{shopIntroduce}}
                </div>

                <div class="main" v-show="ordershow">
                <div class="order">
                <!-- 我的订单主要内容 -->
                <div class="order-content" v-if="orders!==null">
                  <div class="content" v-for="(item,index) in orders" :key="index">
                      <ul>
                        <!-- 我的订单表头 -->
                        <li class="order-info">
                          <div class="order-id">订单编号: {{orders[index].order_id}}</div>
                          <div class="order-time">订单时间: {{orders[index].createTime | dateFormat}}</div>
                          
                        </li>
                        <li class="header">
                          <div class="pro-img"></div>
                          <div class="pro-name">商品名称</div>
                          <div class="pro-price">单价</div>
                          <div class="pro-num">数量</div>
                          <div class="pro-total">小计</div>
                          <div class="pro-operate">操作</div>
              
                        </li>
                        <!-- 我的订单表头END -->

                        <!-- 订单列表 -->
                        <li class="product-list"  >
                          <div class="pro-img">
                            <router-link :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }">
                              <img :src="orders[index].goodsCoverImg" />
                            </router-link>
                          </div>
                          <div class="pro-name">
                            <router-link
                              :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }"
                            >{{orders[index].goodsName}}</router-link>
                          </div>
                          <div class="pro-price">{{orders[index].sellingPrice}}元</div>
                          <div class="pro-num">{{orders[index].goodsCount}}</div>
                          <div class="pro-total pro-total-in">{{orders[index].sellingPrice*orders[index].goodsCount}}元</div>
                        
                          <div class="pro-operate">
                            <el-button type="primary" size="mini" v-show="orders[index].orderStatus == 0" @click="delOrder(orders[index].orderItemId)" >拒绝</el-button>
                            <el-button type="success" size="mini" v-show="orders[index].orderStatus == 1" @click="undateOrder(orders[index].orderItemId,0)">退款</el-button>
                            <el-button type="warning" size="mini" v-show="orders[index].orderStatus == 1" @click="undateOrder(orders[index].orderItemId,2)">发货</el-button>
                            <!-- <el-button type="danger" size="mini" v-show="orders[index].orderStatus == 3">退货</el-button> -->
                            <p v-show="orders[index].orderStatus == 2">等待签收</p>
                            <p v-show="orders[index].orderStatus == 3">订单完成</p>
                          </div>
                        </li>
                      </ul>
                        <div class="order-bar">
                          <div class="order-bar-right">
                            <span>
                              <span class="total-price-title">状态：</span>
                              <span class="total-price">{{orderst[orders[index].orderStatus]}}  </span>
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                      <!-- 订单列表END -->
                </div>
              </div>
                <!-- 主要内容区END -->
      </div>
    <div v-else class="order-empty">
      <div class="empty">
        <h2>您还没有店铺</h2>
        <p>快去开通店铺叭！</p>
    
       <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
          <el-form-item label="名称">
            <el-input v-model="formLabelAlign.name"></el-input>
          </el-form-item>
          <el-form-item label="店铺介绍">
            <el-input v-model="formLabelAlign.region"></el-input>
          </el-form-item>
            <el-form-item>
            <el-button type="primary" @click="onCreatshop">立即创建</el-button>
          </el-form-item>
        </el-form>  
      </div>
    </div>
  </div>
</template>
<script>
// import MyMarkdown from "../components/MyMarkdown";
import 'element-ui/lib/theme-chalk/display.css';
export default {
  components: {
   },
   data(){
     return{
       labelPosition: 'right',
        formLabelAlign: {
          name: '',
          region: '',
        },
        category:1,
      AddItem: {
        name: "",
        introduce: "",
        price:"",
        introducemore:"",
        category:"",
      },param:{},

      shopName:"",
      shopId:"",
      shopIntroduce:"",

       IsShop:false,
       pageSize:15,
       total:0,
       currentPage:0,

       isshow:true,
       ordershow:false,
       addshow:false,
       introduceshow:false,
       orderst:["待支付","已支付","已发货","已签收"],

       orders:"",
         product:"",
        fileList:[],
        rules: {
          name: [
            { required: true, message: '请输入名称', trigger: 'blur' },
          ],
          region: [
            { required: true, message: '请输入店铺介绍', trigger: 'blur' }
          ],
          introduce: [
            {  required: true, message: '请选择日期', trigger: 'blur' }
          ],
          price: [
            { required: true, message: '请填写价格', trigger: 'blur' }
          ],
          introducemore: [
            { required: true, message: '请填写活动形式', trigger: 'blur' }
          ],
          category: [
            { required: true, message: '请选择分类', trigger: 'change' }
          ]
        }
     }
   },
   created(){
      this.isShop() ;
 
   },
  watch: {
     IsShop: function(val) {
      if(val)
      this.getData()
    }
  },
    methods: {
        beforeRemove(file, fileList) {
          console.log(file, fileList);
            },
        //覆盖默认的上传行为
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-3);
      },
       beforeupload(file) {
                console.log(file);
                //创建临时的路径来展示图片
                var windowURL = window.URL || window.webkitURL;
                this.src=windowURL.createObjectURL(file);
                //重新写一个表单上传的方法
                this.param = new FormData();
                this.param.append('file', file, file.name);
                return false;
            },
        establish(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
                this.param.append('goodsName', this.AddItem.name);
                this.param.append('goodsIntro', this.AddItem.introduce);
                this.param.append('goodsDetailContent', this.AddItem.introducemore);
                this.param.append('sellingPrice', this.AddItem.price);
                 this.param.append('storeId', this.shopId);
                this.param.append('goodsCategoryId', this.AddItem.category);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                  //      console.log(this.AddItem.name,this.AddItem.introduce,this.AddItem.price,this.AddItem.category,);
           this.$axios.post(this.$target+ "/store/upload", this.param ,config)
          .then(res => {
            if(res.data.flag){
                console.log(this.orders);
                this.notifySucceed(res.data.msg);
              }
              else{
                this.notifyError("删除失败",res.data.msg);
              }
            })
          .catch(err => {
            return Promise.reject(err);
          }); 
           this.$router.go(0)
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      delOrder(val){
      let as = this.$store.getters.getUserId;
      this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/delOrder",
              method: 'psot',
              params:{
                orderItemId:val,
                userId:as

              }
        })
        .then(res => {
          if(res.data.flag){
               console.log(this.orders);
               this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("删除失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
        this.$router.go(0)

    },
        undateOrder(id ,val){
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/updateOrder",
              method: 'post',
              params:{
                orderItemId:id ,
                status:val ,
              }
        })
        .then(res => {
          if(res.data.flag){
                this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("更新订单失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        });  this.indexorder();
    },



      onCreatshop(){
           this.shopName=this.formLabelAlign.name,
            this.shopIntroduce=this.formLabelAlign.region;
            let as = this.$store.getters.getUserId;
              this.$axios(   
            {
              url: this.$target+"/store/register", 
              // url: "http://localhost:3000/cart", 
              method: 'post',
              params:{
                userId:as,
                storeName:this.formLabelAlign.name,
                introduceSign:this.formLabelAlign.region,
              }
        })
        .then(res => {
          this.IsShop = res.data.flag
           if(res.data.flag)
          {
            this.shopId = res.data.data.storeId
            this.notifySucceed(res.data.msg)
          }
          else{
            this.notifyError(res.data.msg)
          }
         })
        .catch(err => {
          return Promise.reject(err);
        }); 
       
      },
          // 页码变化调用currentChange方法
      currentChange(currentPage) {
      this.currentPage = currentPage;
       
      },
      // 页码变化调用currentChange方法
      isShop() {
              let as = this.$store.getters.getUserId;

             this.$axios(   
            {
              url: this.$target+"/store/isStore", 
              // url: "http://localhost:3000/cart", 
              method: 'get',
              params:{
                userId:as
              }
        })
        .then(res => {
          console.log("res.data.data",res.data.data);

          this.IsShop =res.data.flag;
          if(res.data.flag)
          { 
            this.shopName=res.data.data.storeName,
            this.shopId=res.data.data.storeId,
            this.shopIntroduce=res.data.data.introduceSign,
            this.notifySucceed(res.data.msg)
          }
          else{
            this.notifyError(res.data.msg)
          }
         //  this.total = res.data.total;
        })
        .catch(err => {
          return Promise.reject(err);
        }); 
       
      },
 
      handleClick(tab, event) {
        console.log(tab, event);
        console.log(tab.index);
 

        if(tab.index==0){
        this.isshow = true
        this.ordershow = false
        this.addshow=false
        this.introduceshow=false
        }
        else if(tab.index==1){
        this.isshow = false
        this.ordershow = false
        this.addshow=true
        this.introduceshow=false
        }
        else if(tab.index==2){
        this.isshow = false
        this.ordershow = true
        this.addshow=false
        this.introduceshow=false
         this.indexorder();
        }
        else if(tab.index==3){
        this.isshow = false
        this.ordershow = false
        this.addshow=false
        this.introduceshow=true
         }
        else{
          console.log("error");
        }
         console.log("this.isshow",this.isshow);
         console.log(" isshow",this.orders);
      },
      getData() {
      // 如果分类列表为空则请求全部商品数据，否则请求分类商品数据
        const api = "/store/allGoods"; //请求分类数据
       this.$axios(   
            {
              url: this.$target+api, 
              // url: "http://localhost:3000/cart", 
              method: 'get',
              params:{
               StoreId:this.shopId,
 
              }
        })
        .then(res => {
           this.product = res.data.data;
        //  this.total = res.data.total;
        })
        .catch(err => {
          return Promise.reject(err);
        }); 
    },
    indexorder(){
        this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/store/allOrders",
              method: 'get',
              params:{
                  StoreId:this.shopId,  
              }
        })
        .then(res => {
           if(res.data.flag){
              this.orders = res.data.data;
             }
            else{
              this.notifyError("获取所有订单",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
    }
    }

};
</script>
<style scoped>
