}
/* 订单列表CSS END */

/* 订单为空的时候显示的内容CSS */
 .order-empty {
  width: 1225px;
  margin: 0 auto;
}
 .order-empty .empty {
  height: 300px;
  padding: 0 0 130px 558px;
  margin: 65px 0 0;
  background: url(../assets/imgs/cart-empty.png) no-repeat 124px 0;
  color: #b0b0b0;
  overflow: hidden;
}

.order-empty .empty h2 {
  margin: 70px 0 15px;
  font-size: 36px;
}
  .order-empty .empty p {
  margin: 0 0 20px;
  font-size: 20px;
}
</style>
